import Topbar from '../../components/topBar/topbarx.vue'
export default {
	data() {
		return {
				title: "修改密码",
				oldPassword: '', // 旧密码
				newPassword: '', // 新密码
				alignNewPassword: ''
		}
	},
	methods: {
		// 修改密码
		subClick(){
			let userInfo = this.$store.state.userInfo
			console.log('userinfo', userInfo)
			if(this.newPassword != this.alignNewPassword){
				uni.showModal({
					title: '提示',
					content: '新密码与旧密码不一致！'
				})
				return
			}
			console.log('newPassword,', this.newPassword)
			console.log('alignNewPassword,', this.alignNewPassword)
			console.log('user_id,', userInfo.user_id)
			console.log('token,', userInfo.token)
			uni.request({
				url: this.$http + '/api/user/changepwd',
				method: 'POST',
				data: {
					newpassword: this.newPassword,
					password: this.oldPassword,
					user_id: userInfo.user_id,
					token: userInfo.token
				},
				success(res){
					console.log('修改密码返回数据',res)
					if(res.data.code == 1){
						uni.showModal({
							title: '提示',
							content: res.data.msg
						})
					}else{
						uni.showModal({
							title: '提示',
							content: res.data.msg
						})
					}
				}
			})
		},
		// 返回上级目录
		goBack(){
			uni.navigateTo({
				url: '../modifyperson/modifyperson'
			})
		}
	},
	components: {
		Topbar
	}
}
